import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BlogsComponent, BlogDetailComponent} from './blogs'
import * as serviceWorker from './serviceWorker';

const appEl = document.getElementById('root')
if (appEl) {
    ReactDOM.render(<App />, appEl);
}
const e = React.createElement
const blogsEl = document.getElementById("ApexGaming")
if (blogsEl) {
    ReactDOM.render(
        e(BlogsComponent, blogsEl.dataset), blogsEl);
}

const blogDetailElements = document.querySelectorAll(".ApexGaming-detail")

blogDetailElements.forEach(container=> {
    ReactDOM.render(
        e(BlogDetailComponent, container.dataset),
        container);
})
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
